body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
button{
  display: -ms-flexbox;
  display: flex;
  border-radius: 0;
  padding: 1em;
  font-weight: bold;
  font-size: 1em;
  background-color: #ff1a1a;
  color: whitesmoke;
}
.gameTitle{
  margin-top: 0;
  text-align: center;
  background-image: url("Ocean.jpg");
  padding: 1em;
  background-size: cover;
  background-position: bottom;
  color: whitesmoke;
  font-weight: 600;
  font-size: 3.5em;
  letter-spacing: 0.5em;
  text-shadow: 2px 2px #444;
}

.main{
  display: -ms-flexbox;
  display: flex;
}
.gameArea{
  width: 80%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

.menuComponents{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 20%;
}

.checkWin{
  color: red;
  text-align: center;
}
.menu{
  text-align: center;
  margin: 1em;
}
.playersView{
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.playersView h1 {
  width: 100%;
  text-align: center;
}
.playerBoard{
  display: -ms-flexbox;
  display: flex;
  width: 400px;
  height: 400px;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  margin-bottom: 5em;
}
.playerComputerBoard {
  display: -ms-flexbox;
  display: flex;
  width: 400px;
  height: 400px;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}
.cells{
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 40px;
  height: 40px;
  border: 2px solid black;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.rows{
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 40px;
}
.hit{
  background-color: red;
}
.miss{
  background-color: blue;
}
.start{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

/*Ship Status Start*/
.shipStatus{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-line-pack: distribute;
      align-content: space-around;
  line-height: 1.5em;
  text-align: center;
}
.shipStatus ul{
  list-style: none;
  padding: 0;
}
.afloat{
  color: green;
}
.sunk{
  color: red;
  text-decoration: line-through;
}
/*Ship Status End*/



